// src/App.js
import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import CircularProgress from '@mui/material/CircularProgress';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import ExitToAppIcon from '@mui/icons-material/ExitToApp';
import axios from 'axios';
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer
} from 'recharts';
import './App.css';

// axios  
axios.defaults.baseURL = 'http://localhost:8080';

//  
const theme = createTheme({
  palette: {
    mode: 'light',
    primary: {
      main: '#1976d2',
    },
    secondary: {
      main: '#dc004e',
    },
  },
});

//  
function Login({ onLogin }) {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      console.log(' :', { username, password });
      const response = await axios.post('/api/auth/login', { username, password });
      console.log(' :', response.data);
      localStorage.setItem('token', response.data.token);
      onLogin(response.data.token);
    } catch (err) {
      console.error(' :', err);
      if (err.response) {
        console.error(' :', err.response.data);
        setError(err.response.data.error || ' .');
      } else if (err.request) {
        console.error(' :', err.request);
        setError('   .   .');
      } else {
        console.error(' :', err.message);
        setError('    .');
      }
    }
  };

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        minHeight: '100vh',
      }}
    >
      <Paper sx={{ p: 4, maxWidth: 400, width: '100%' }}>
        <Typography variant="h5" component="h1" gutterBottom>
          
        </Typography>
        {error && (
          <Typography color="error" gutterBottom>
            {error}
          </Typography>
        )}
        <form onSubmit={handleSubmit}>
          <TextField
            fullWidth
            label=""
            margin="normal"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
          />
          <TextField
            fullWidth
            label=""
            type="password"
            margin="normal"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
          <Button
            fullWidth
            variant="contained"
            color="primary"
            type="submit"
            sx={{ mt: 2 }}
          >
            
          </Button>
        </form>
      </Paper>
    </Box>
  );
}

//  
function Header({ onLogout }) {
  return (
    <AppBar position="static" sx={{ mb: 4 }}>
      <Toolbar>
        <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
             
        </Typography>
        <IconButton color="inherit" onClick={onLogout} title="">
          <ExitToAppIcon />
        </IconButton>
      </Toolbar>
    </AppBar>
  );
}

//   
function Dashboard({ onLogout }) {
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [shippingData, setShippingData] = useState([]);
  const [latestData, setLatestData] = useState(null);

  useEffect(() => {
    let isMounted = true;
    
    const fetchData = async () => {
      try {
        setLoading(true);
        const token = localStorage.getItem('token');
        const config = {
          headers: { Authorization: `Bearer ${token}` }
        };

        console.log('  ...');
        
        //    
        const sampleData = {
          date: new Date().toISOString().split('T')[0],
          indices: [
            {
              name: "BDI (Baltic Dry Index)",
              value: "1,432",
              change: "+15",
              source: "Baltic Exchange"
            },
            {
              name: "BCI (Baltic Capesize Index)",
              value: "1,876",
              change: "+23",
              source: "Baltic Exchange"
            },
            {
              name: "SCFI ",
              value: "834.23",
              change: "-12.5",
              source: "   "
            },
            {
              name: "  VLSFO",
              value: "685.50",
              change: "-2.5",
              source: " "
            }
          ]
        };
        
        let fetchedLatestData = null;
        let fetchedHistoryData = [];
        
        //    
        try {
          const response = await axios.get('/api/indices/latest', config);
          console.log('   :', response.data);
          fetchedLatestData = response.data;
          if (isMounted) setLatestData(response.data);
        } catch (latestError) {
          console.error('   :', latestError);
          if (isMounted) {
            //    
            setLatestData(sampleData);
            fetchedLatestData = sampleData;
          }
        }
        
        //    
        try {
          const historyResponse = await axios.get('/api/indices', config);
          console.log('   :', historyResponse.data);
          
          //     
          fetchedHistoryData = Array.isArray(historyResponse.data) 
            ? historyResponse.data 
            : [historyResponse.data];
          
          if (isMounted) setShippingData(fetchedHistoryData);
        } catch (historyError) {
          console.error('   :', historyError);
          //    (  )
          fetchedHistoryData = [fetchedLatestData || sampleData];
          if (isMounted) setShippingData(fetchedHistoryData);
        }
      } catch (err) {
        console.error('    :', err);
        if (err.response?.status === 401) {
          localStorage.removeItem('token');
          window.location.reload();
        } else {
          if (isMounted) setError('    .');
        }
      } finally {
        if (isMounted) setLoading(false);
      }
    };

    fetchData();
    
    return () => {
      isMounted = false; //     
    };
  }, []); //    -    

  //   
  if (loading) {
    return (
      <Box display="flex" justifyContent="center" alignItems="center" minHeight="100vh">
        <CircularProgress />
      </Box>
    );
  }

  //   
  const chartData = [];
  
  //    
  if (shippingData && shippingData.length > 0) {
    shippingData.forEach(item => {
      if (item && item.date && item.indices) {
        const bdiItem = item.indices.find(i => i.name && i.name.includes('BDI'));
        if (bdiItem) {
          const bdiValue = parseFloat(String(bdiItem.value || "0").replace(/,/g, '')) || 0;
          chartData.push({
            date: item.date,
            BDI: bdiValue
          });
        }
      }
    });
  }
  
  //     
  if (chartData.length === 0) {
    chartData.push({ date: '2023-01-01', BDI: 1500 });
    chartData.push({ date: '2023-01-02', BDI: 1550 });
    chartData.push({ date: '2023-01-03', BDI: 1600 });
  }

  return (
    <Box sx={{ flexGrow: 1 }}>
      <Header onLogout={onLogout} />
      <Container maxWidth="lg">
        <Typography variant="h4" component="h1" gutterBottom>
            
        </Typography>
        
        {/*    */}
        {latestData && latestData.indices && latestData.indices.length > 0 ? (
          <Grid container spacing={3} sx={{ mb: 4 }}>
            {latestData.indices.map((index, idx) => (
              <Grid item xs={12} sm={6} md={4} key={idx}>
                <Paper sx={{ p: 2 }}>
                  <Typography variant="h6">{index.name || ""}</Typography>
                  <Typography variant="h4">{index.value || "N/A"}</Typography>
                  <Typography 
                    color={index.change && index.change.startsWith('+') ? 'success.main' : 'error.main'}
                    variant="subtitle1"
                  >
                    {index.change || "0"}
                  </Typography>
                  <Typography variant="caption" color="text.secondary">
                    {index.source || " "}
                  </Typography>
                </Paper>
              </Grid>
            ))}
          </Grid>
        ) : (
          <Paper sx={{ p: 3, mb: 4, bgcolor: '#fff8e1' }}>
            <Typography color="warning.main">
              {error || "   .    ."}
            </Typography>
          </Paper>
        )}

        {/*   */}
        <Paper sx={{ p: 2 }}>
          <Typography variant="h6" gutterBottom>
            BDI 
          </Typography>
          <Box sx={{ height: 400 }}>
            <ResponsiveContainer width="100%" height="100%">
              <LineChart
                data={chartData}
                margin={{ top: 5, right: 30, left: 20, bottom: 5 }}
              >
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="date" />
                <YAxis />
                <Tooltip />
                <Legend />
                <Line type="monotone" dataKey="BDI" stroke="#8884d8" />
              </LineChart>
            </ResponsiveContainer>
          </Box>
        </Paper>
      </Container>
    </Box>
  );
}

//  App 
function App() {
  const [isAuthenticated, setIsAuthenticated] = useState(!!localStorage.getItem('token'));

  const handleLogin = (token) => {
    setIsAuthenticated(true);
  };
  
  const handleLogout = () => {
    localStorage.removeItem('token');
    setIsAuthenticated(false);
  };

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Router>
        <Routes>
          <Route
            path="/"
            element={
              isAuthenticated ? (
                <Dashboard onLogout={handleLogout} />
              ) : (
                <Navigate to="/login" replace />
              )
            }
          />
          <Route
            path="/login"
            element={
              isAuthenticated ? (
                <Navigate to="/" replace />
              ) : (
                <Login onLogin={handleLogin} />
              )
            }
          />
        </Routes>
      </Router>
    </ThemeProvider>
  );
}

export default App;
